@charset "UTF-8";
/**
 * Rem-converter mixin to convert px to rem
 */
.callme {
  border: 1px solid #005F9F;
  -webkit-border-radius: 50px;
  -moz-border-radius: 50px;
  border-radius: 50px;
  font-weight: 600;
  line-height: 20px;
  text-align: center;
  width: 70%;
  padding: 15px 0;
}
.callme a {
  display: block;
  color: #005F9F;
  text-decoration: none;
  font-size: 16px;
  font-size: 1rem;
  font-weight: 600;
  text-transform: uppercase;
}

.colored {
  background-color: #005F9F;
  width: 40%;
}
.colored a {
  color: #ffffff;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html, body {
  width: 100%;
  height: 100%;
}

body {
  background-color: #ffffff;
  display: -webkit-flex;
  display: -ms-flex;
  display: flex;
  flex-direction: column;
  min-width: 320px;
  font-family: "Montserrat", sans-serif;
}

img {
  display: block;
  height: auto;
  max-width: 100%;
}

ul, ol {
  list-style: none;
}

a {
  text-decoration: none;
}

header,
footer {
  flex: 0 0 auto;
}

.wrapper {
  /* 1 flex-grow, 0 flex-shrink, auto flex-basis */
  flex: 1 0 auto;
}

h2,
h3, h4 {
  color: #005F9F;
}

.container {
  max-width: 1200px;
  width: 100%;
  margin: 0 auto;
}

header {
  padding-top: 40px;
}
header .header {
  display: -webkit-flex;
  display: -ms-flex;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
header .header .navigation {
  display: -webkit-flex;
  display: -ms-flex;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 666px;
}

.nav-top {
  width: 100%;
  margin-right: 40px;
}

.nav-footer {
  width: 470px;
}

nav ul {
  display: -webkit-flex;
  display: -ms-flex;
  display: flex;
  justify-content: space-around;
  align-items: center li;
  align-items-display: block;
}
nav ul a {
  font-size: 18px;
  font-size: 1.125rem;
  font-weight: 500;
  color: #011e32;
}
nav ul a:hover {
  color: #005F9F;
}

.promo_block {
  margin-top: 110px;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
}
.promo_block .promo_text h1 {
  font-size: 50px;
  font-size: 3.125rem;
  font-weight: 500;
}
.promo_block .promo_text p {
  font-size: 24px;
  font-size: 1.5rem;
  line-height: 37px;
  margin: 30px 0 54px 0;
}
.promo_block .promo_img {
  max-width: 530px;
  width: 100%;
  justify-self: end;
}

.blocks {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-column-gap: 0.8em;
  grid-row-gap: 5em;
  margin: 108px 0 0;
}
.blocks .block {
  background-color: #f6f6f6;
  -webkit-border-radius: 10px;
  -moz-border-radius: 10px;
  border-radius: 10px;
  padding: 0 34px;
  height: 246px;
  display: grid;
  justify-items: center;
  align-items: center;
}
.blocks .block p {
  font-size: 24px;
  font-size: 1.5rem;
  font-weight: 400;
  line-height: 37px;
}

.technology_block {
  margin-top: 70px;
}
.technology_block h3 {
  font-size: 40px;
  font-size: 2.5rem;
  line-height: 49px;
  font-weight: 500;
  margin-bottom: 55px;
}
.technology_block .technologies {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-row-gap: 4em;
}
.technology_block .technologies .tech {
  display: -webkit-flex;
  display: -ms-flex;
  display: flex;
  align-items: center;
}
.technology_block .technologies .tech span {
  font-size: 28px;
  font-size: 1.75rem;
  line-height: 43px;
  font-weight: 500;
  color: #011e32;
  margin-left: 20px;
}

.tabs_block {
  display: grid;
  grid-template-columns: 500px repeat(1, 1fr);
  grid-column-gap: 60px;
}

.tabs {
  display: flex;
  flex-direction: column;
  background-color: #F6F6F6;
  -webkit-border-radius: 10px;
  -moz-border-radius: 10px;
  border-radius: 10px;
}

.tabs li:not(:last-child) {
  margin-right: 7px;
}

.tabs li a {
  display: block;
  position: relative;
  top: 4px;
  padding: 10px 25px;
  border-radius: 2px 2px 0 0;
  opacity: 0.7;
  transition: all 0.1s ease-in-out;
}

.tabs li.active a,
.tabs li a:hover {
  opacity: 1;
}

.tabs-content {
  position: relative;
  z-index: 2;
}

.tabs-panel {
  display: none;
}

.tabs-panel.active {
  display: block;
}
.tabs-panel.active h3 {
  color: #011e32;
  font-size: 40px;
  font-size: 2.5rem;
  font-weight: 500;
}
.tabs-panel.active p {
  font-size: 18px;
  font-size: 1.125rem;
  line-height: 28px;
  margin-top: 30px;
}

.tabs-panel p + div {
  margin-top: 15px;
}

@media screen and (max-width: 600px) {
  .tabs {
    flex-direction: row;
  }

  .tabs li {
    width: 100%;
  }

  .tabs li:not(:last-child) {
    margin-right: 0;
  }

  .tabs li a {
    border-radius: 0;
    opacity: 1;
    top: 0;
  }

  .tabs li.active a::before {
    content: "•";
    padding-right: 5px;
  }

  .tabs-content {
    border-radius: 0;
  }
}
footer {
  background-color: #F6F6F6;
}
footer .footer {
  display: -webkit-flex;
  display: -ms-flex;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 60px 0 30px 0;
}
footer .footer .logo {
  width: 72px;
  height: 60px;
}
footer .footer .footer_icon ul {
  display: -webkit-flex;
  display: -ms-flex;
  display: flex;
}
footer .footer .footer_icon ul li {
  margin-right: 30px;
}
footer .footer .footer_icon ul li:last-child {
  margin: 0;
}
footer .footer .footer_icon ul li a {
  display: block;
  width: 24px;
  height: 24px;
}
footer .copyright {
  width: 270px;
  margin: 30px auto;
}
footer .copyright span {
  font-size: 17px;
  font-size: 1.0625rem;
  font-weight: 500;
  color: #879198;
}